import React from "react";
import styles from "./Skills.module.css";

const Skills = () => {
  return (
    <section className={styles.container}>
      <div className={styles.skills_container}>
        <h3 className={styles.title}>I can write in:</h3>
        <ul className={styles.list}>
          <li className={styles.item}>
            <p className={styles.text}>HTML</p>
          </li>
          <li className={styles.item}>
            <p className={styles.text}>CSS</p>
          </li>
          <li className={styles.item}>
            <p className={styles.text}>Sass</p>
          </li>
          <li className={styles.item}>
            <p className={styles.text}>Bootstrap</p>
          </li>
          <li className={styles.item}>
            <p className={styles.text}>Git</p>
          </li>
          <li className={styles.item}>
            <p className={styles.text}>JavaScript</p>
          </li>
          <li className={styles.item}>
            <p className={styles.text}>React</p>
          </li>
        </ul>
      </div>
      <div className={styles.tools_container}>
        <h3 className={styles.title}>To do my work I use:</h3>
        <ul className={styles.list}>
          <li className={styles.item}>
            <p className={styles.text}>Google Chrome</p>
          </li>
          <li className={styles.item}>
            <p className={styles.text}>Visual Studio Code</p>
          </li>
          <li className={styles.item}>
            <p className={styles.text}>CodePen</p>
          </li>
          <li className={styles.item}>
            <p className={styles.text}>Terminal</p>
          </li>
          <li className={styles.item}>
            <p className={styles.text}>GitHub</p>
          </li>
          <li className={styles.item}>
            <p className={styles.text}>NPM</p>
          </li>
        </ul>
      </div>
    </section>
  );
};

export default Skills;
