import React from "react";
import styles from "./Hero.module.css";
import photo from "../img/fota_small.jpg";

const Hero = () => {
  return (
    <section id="about" className={styles.about}>
      <div className={styles.hero}>
        <h1 className={styles.hero_title}>
          Hi, i'm <span className={styles.hero_accent}>Kamil</span>
        </h1>
        <p className={styles.hero_aboutme}>
          I'm junior web developer from
          <span className={styles.hero_accent}> Bytom</span> in
          <span className={styles.hero_accent}> Poland</span>.
          <br />I love music, new technologies and cats.
          <br />
          Welcome to my page ðŸ˜Š
        </p>
        <nav className={styles.hero_nav_social}>
          <ul className={styles.hero_nav_list}>
            <li className={styles.hero_nav_item}>
              <a href="https://github.com/kamilwojtol">
                <button className={styles.hero_button_social}>
                  <i class="fab fa-github"></i>
                </button>
              </a>
            </li>
            <li className={styles.hero_nav_item}>
              <a href="https://twitter.com/kwojtol">
                <button className={styles.hero_button_social}>
                  <i class="fab fa-twitter"></i>
                </button>
              </a>
            </li>
            <li className={styles.hero_nav_item}>
              <a href="https://www.linkedin.com/in/kamil-wojtol/">
                <button className={styles.hero_button_social}>
                  <i class="fab fa-linkedin"></i>
                </button>
              </a>
            </li>
          </ul>
        </nav>
      </div>
      <img className={styles.hero_photo} src={photo} alt="Kamil Wojtol"></img>
    </section>
  );
};

export default Hero;
