import React from "react";
import styles from "./Header.module.css";

const Header = () => {
  return (
    <header className={styles.header}>
      <nav className={styles.header_nav}>
        <ul className={styles.header_list}>
          <li className={styles.header_item}>
            <a className={styles.header_link} href="#about">
              About
            </a>
          </li>
          <li className={styles.header_item}>
            <a className={styles.header_link} href="#skills">
              Skills
            </a>
          </li>
          <li className={styles.header_item}>
            <a className={styles.header_link} href="#projects">
              Projects
            </a>
          </li>
          <li className={styles.header_item}>
            <a className={styles.header_link} href="#contact">
              Contact
            </a>
          </li>
          <li className={styles.header_item}>
            <button className={styles.header_button}>
              <a
                className={styles.header_mail}
                href="mailto:kamil.wojtol97@gmail.com"
              >
                <i className="far fa-envelope"></i>
              </a>
            </button>
          </li>
        </ul>
      </nav>
    </header>
  );
};

export default Header;
